@media (max-width: 950px) {
  .visible {
    display: block;
  }

  .notVisible {
    display: none;
  }

  .navButton {
    display: block;

    width: 50px;
    height: 50px;
  }

  .mobileVersion {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-around;
  }

  .searchSection {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}